<template>
    <Row type="flex" justify="center">
        <Col  span="13" style="margin-right:10px;">
            <card style="min-height:800px;">
                <div class="title">{{blog.title}}</div>
                <div>{{blog.createTime}},{{blog.view_count}}</div>
                <div v-html="blog.content"></div>
            </card>
            <card style="margin-top:10px;">
                <ul class="commentlist">
                    <li class="comment" v-for="ele in comments" :key="ele.id">
                        <div class="comment_body">
                            <div class="comment_meta">
                                <div class="avatar-box">
                                <!-- <div class="avatar" style="width:48px;height:48px;"></div> -->
                                    <div style="display:flex;justify-content: center;">
                                        <Avatar :style="{background: '#cbd7df'}">{{ ele.username.substring(0,1) }}</Avatar>
                                    </div>

                                </div> {{ele.username}}
                            </div>
                            <div class="comment_content">
                                <p>{{ele.content}}</p>
                                    <div class="comm_meta_div">
                                        <span class="commentmetadata" style="display: none;"><a rel="nofollow" class="comment-reply-link" href="#comment-139045" data-commentid="139045" data-postid="1877" data-belowelement="comment-139045" data-respondelement="respond" aria-label="回复给YYannis"><i class="ipsicon ipsicon-reply ipsicon-rspace"></i>回复他/她&nbsp;&nbsp;</a></span> 
                                    <i class="ipsicon ipsicon-time ipsicon-rspace"></i>{{ele.createTime}}
                                </div>
                            </div>
                        </div>
                    </li>
                </ul>
                <Input v-model="comment" type="textarea" :rows="4" placeholder="Enter something..." />
                <div style="display:flex;justify-content:space-between;margin-top:10px">
                    <span></span>
                    <Button type="primary" :loading="loading" @click="addComment">留言</Button>
                </div>
            </card>
        </Col>
        <Col span="5">

        </Col>
    </Row>
</template>
<script>
import axios from 'axios'
import marked from 'marked'
export default {
    data(){
        return {
            blog:{},
            comments:[],
            comment:'',
            loading:false,
        }
    },
    beforeRouteEnter (to, from, next) {
        let blogId= to.params.id
        axios(`/api/blogs/${blogId}`).then(res=>{
            next(vm => {
                vm.blog=res.data
                vm.blog.content=marked(vm.blog.content)
                vm.getComments()
            })
        })
    },
    methods:{
        addComment(){
            this.loading=true
            axios({
                url:'/api/comment',
                method:'post',
                data:{
                    blogId:this.$route.params.id,
                    content:this.comment
                }
            }).then(res=>{
                this.loading=false
                this.getComments()
            })
            this.comment=''
        },
        getComments(){
            axios(`/api/blog/${this.$route.params.id}/comment`).then(res=>{
                this.comments=res.data.data
            })
        }
    }
}
</script>
